import React, { useState, useContext, useEffect } from 'react'
import Calendar from 'react-calendar';
import TimePicker from 'rc-time-picker';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronDown, faChevronUp } from "@fortawesome/free-solid-svg-icons";
import moment from 'moment'

import 'react-calendar/dist/Calendar.css';
import 'rc-time-picker/assets/index.css';

import { DateMonth, CalendarNavigation, CalendarNavigationBtnContainer, CalendarNavigationBtn, CalendarHourContainer, CalendarContainer,
    CalendarHour, TypeDateContainer, TypeDate } from '../../styles/Date'

import { appContext } from '../../context/Provider'

function CalendarComponent({ dateType, setDateType }) {

    const context = useContext(appContext)

    const monthNames = ["Enero", "Febrero", "Marzo", "Abril", "Mayo", "Junio", "Julio", "Agosto", "Septiembre", "Octubre", "Noviembre", "Diciembre"];

    const [date, setDate] = useState(new Date())
    const [time, setTime] = useState(moment().set('minute', 0).set('hour', 0)) 
    const [doctorType, setDoctorType] = useState(true)

    const onChangeCalendar = (newDate) => setDate(newDate)

    const onChangeTime = newTime => setTime(newTime)

    const nextMonth = () => {
        setDate(null)
        setTimeout(() => {setDate(new Date(date.setMonth( date.getMonth() +1 )))}, 10);
    }

    const lastMonth = () => {
        setDate(null)
        setTimeout(() => {setDate(new Date(date.setMonth( date.getMonth() -1 )))}, 10);
    }

    const setDoctorTypeDate = () => {
        setDoctorType(true)
        setDateType('medico')
    }
    const setLabTypeDate = () => {
        setDoctorType(false)
        setDateType('laboratorio')
    }

    useEffect(() => {
        const hour = time.toString().split(' ')[4]
        let dateSelected = moment(date).set('hour',hour.split(':')[0]).set('minute',hour.split(':')[1]).utc().valueOf()
        
        context.dispatchDate({ type: 'SET', value: dateSelected })
    }, [date, time])

    useEffect(() => {
        context.dispatchDateType({ type: 'SET', value: doctorType ? 'medico' : 'laboratorio' })
        if(!doctorType){
            context.dispatchDoctorDate({ type: 'SET', value: {} })
        }
    }, [doctorType])

    useEffect(() => {
        if(dateType === "laboratorio"){ setDoctorType(false) }
        else{ setDoctorType(true) }
    }, [dateType])

    return (
        <>
            <CalendarContainer>
                <CalendarNavigation>
                    <DateMonth>{date && `${monthNames[date.getMonth()]} ${date.getFullYear()}`}</DateMonth>
                    <CalendarNavigationBtnContainer>
                        <CalendarNavigationBtn onClick={nextMonth}>
                            <FontAwesomeIcon 
                                icon={faChevronUp} 
                                color="rgb(0,47,91)" 
                                size="2x" 
                                className="arrow-icon"
                            />
                        </CalendarNavigationBtn>
                        <CalendarNavigationBtn onClick={lastMonth}>
                            <FontAwesomeIcon 
                                icon={faChevronDown} 
                                color="rgb(0,47,91)" 
                                size="2x" 
                                className="arrow-icon"
                            />
                        </CalendarNavigationBtn>
                    </CalendarNavigationBtnContainer>
                </CalendarNavigation>
                {date && (
                    <Calendar
                        onChange={onChangeCalendar}
                        value={date}
                        showNavigation={false}
                    />
                )}
            </CalendarContainer>
            <CalendarHourContainer>
                <CalendarHour>
                    <span>HORA</span>
                    <TimePicker
                        value={time}
                        onChange={onChangeTime}
                        showSecond={false}
                    />
                </CalendarHour>
                <TypeDateContainer>
                    <TypeDate doctorType={doctorType} onClick={setDoctorTypeDate}>MEDICOS</TypeDate>
                    <TypeDate doctorType={!doctorType} onClick={setLabTypeDate}>LABORATORIOS</TypeDate>
                </TypeDateContainer>
            </CalendarHourContainer>
        </>
    )
}

export default CalendarComponent
