import styled from 'styled-components'

//const purple = "rgb(88,32, 142)"
const darkBlue = "rgb(0,47,91)"
const lightBlue = "rgb(178,223,252)"

export const DoctorsGroupContainer = styled.div`
    width: ${props => props.width || '80vw'};
    min-height: 5vw;
    height: ${props => props.height || 'fit-content'};
`

export const DoctorsTitleContainer = styled.div`
    width: 76vw;
    height: 5vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 2vw;
    border-bottom: 0.2vw solid ${darkBlue};
    margin-bottom: 2vw;

    h1{
        font-family: Trajan;
        color: ${darkBlue};
        font-size: 2vw;
        margin: 0;
    }
`

export const DoctorsTitleButton = styled.button`
    border: 0;
    background-color: transparent;
    width: 3vw;
    padding: 0 auto;

    &:hover{ cursor: pointer; }
    &:focus{ outline: none }
`

export const DoctorsGroupContentContainer = styled.div`
    width: ${props => props.width || '80vw'};
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    transition: height 500ms ease-in-out;
    height: ${props => props.height};
    overflow-y: hidden;
`

export const DoctorItemContainer = styled.div`
    width: ${props => props.width};
    display: flex;
    height: 8vw;
    display: flex;
    align-items: center;
    padding: 0.5vw 1vw;
    background-color: ${props => props.fillBg ? lightBlue : 'white'};
    &:hover{ cursor: pointer; }
`

export const DoctorImage = styled.img`
    width: 7vw;
    height: 7vw;
    border-radius: 1vw;
    object-fit: cover;
`

export const DoctorInfo = styled.div`
    margin-left: 1vw;
    display: flex;
    justify-content: center;
    flex-direction: column;
`

export const DoctorName = styled.h2`
    margin: 0;
    font-family: MontserratMedium;
    color: ${darkBlue};
    font-size: 1vw;
    margin-bottom: 0.2vw;
`

export const DoctorType = styled.h2`
    margin: 0;
    font-family: MontserratMedium;
    color: rgb(150,150,150);
    font-size: 0.9vw;
`

export const DoctorStars = styled.div`
    display: flex;
    align-items: center;
    margin: 0.4vw 0;
`

export const DoctorDescription = styled.p`
    margin: 0;
    font-family: Montserrat;
    color: rgb(150,150,150);
    font-size: 0.8vw;
    text-align: justify;
    &:hover{ cursor: auto }
`

export const DoctorDescriptionContainer = styled.div`
    display: flex;
    align-items: center;
    margin: 0.2vw 0;
`

export const DoctorLink = styled.span`
    font-family: Montserrat;
    color: ${darkBlue};
    font-size: 0.8vw;
    margin-left: 0.5vw;
`