import React from 'react'
import Select from 'react-select';

import { InputContainer, InputLabel, InputForm, TextareaForm } from '../../styles/SignUp'

const options = [
    { value: 'A+', label: 'A+' },
    { value: 'A-', label: 'A-' },
    { value: 'B+', label: 'B+' },
];

function Input({ label, value, setValue, id, number, textarea, type }) {

    const handleInput = (e) => {
        if(number){ setValue(e.target.value.replace(/[^0-9]/g, '')) }
        else{ setValue(e.target.value) }
    }

    return (
        <InputContainer>
            <InputLabel htmlFor={id}>{label}</InputLabel>
            {id === "blood" ? (
                <Select
                    value={value}
                    onChange={setValue}
                    options={options}
                    className="select-signup"
                    classNamePrefix="select"
                    placeholder="Tipo de sangre"
                />
            ): (
                textarea ? (
                    <TextareaForm 
                        value={value}
                        onChange={handleInput}
                        id={id}
                    />
                ):(
                    <InputForm 
                        value={value}
                        onChange={handleInput}
                        id={id}
                        type={type || 'text'}
                    />
                )
            )}
        </InputContainer>
    )
}

export default Input
