import { createGlobalStyle } from 'styled-components'

import TrajanFont from '../assets/fonts/trajan-pro.otf'
import MontserratBoldFont from '../assets/fonts/Montserrat-Bold.ttf'
import MontserratFont from '../assets/fonts/Montserrat-Regular.ttf'
import MontserratMediumFont from '../assets/fonts/Montserrat-Medium.ttf'
import MontserratItalicFont from '../assets/fonts/Montserrat-Italic.ttf'
import MontserratLightFont from '../assets/fonts/Montserrat-Light.ttf'

const GlobalStyle = createGlobalStyle`

    @font-face {
        font-family: Trajan;
        src: url(${TrajanFont});
    }

    @font-face {
        font-family: Montserrat;
        src: url(${MontserratFont});
    }

    @font-face {
        font-family: MontserratBold;
        src: url(${MontserratBoldFont});
    }

    @font-face {
        font-family: MontserratMedium;
        src: url(${MontserratMediumFont});
    }

    @font-face {
        font-family: MontserratItalic;
        src: url(${MontserratItalicFont});
    }

    @font-face {
        font-family: MontserratLight;
        src: url(${MontserratLightFont});
    }

    #root, body, html{
        width: 100%;
        height: 100%;
        margin: 0;
    }

    .carousel-image-container{
        width: 100%;
        height: 100%;
        display: flex;
        flex-wrap: nowrap;
    }

    #circle-login-form{
        position: relative;
        top: -5vw;
        z-index: 0;
    }

    .icon-lab{
        width: 7vw;
        height: 7vw;
    }

    .icon-medium-lab{
        width: 5vw;
        height: 5vw;
    }

    .icon-small-lab{
        width: 2vw;
        height: 2vw;
    }

    .postreview-icon-btn{ font-size: 1.5vw; }

    .star-icon{ font-size: 1.1vw; }

    .arrow-icon{ font-size: 1.5vw; }

    #root{ overflow-y: hidden; }

    .select-signup{
        width: 60%;
        font-family: Montserrat;
        border: 0;
        
    }

    .css-yk16xz-control{
        border: 0 !important;
        background-color: rgb(235,235,235) !important;
    }

    .css-1pahdxg-control{
        border: 0 !important;
        background-color: rgb(235,235,235) !important;
        &:hover{
            border-color: rgb(0,47,91) !important;
        }
    }
    
`

export default GlobalStyle