import React, { useEffect, useState, useRef, useContext } from 'react'
import axios from 'axios'

import MainContainer from '../components/MainContainer'
import FormGroup from '../components/date/FormGroup'

import {  TitleContainer } from '../styles/Date'

import { URL } from '../variables'
import { appContext } from '../context/Provider'

function Date({ location }) {

    const context = useContext(appContext)

    const refTitle = useRef(null)

    const [currentIdx, setCurrentIdx] = useState(0)
    const [type, setType] = useState('medico')
    const [info, setInfo] = useState({})

    const getInfo = async (id, infoType) => {
        let res = await axios({
            url: `${URL}/${infoType}/${id}?reviews=false`,
            method: 'GET',
            timeout: 5000
        })
        return res.data
    }

    const checkQueries = () => {
        const query = new URLSearchParams(location.search);
        if(query.get('id') && query.get('type')){
            if(query.get('type') !== "medico" && query.get('type') !== "laboratorio"){
                window.location.replace('/cita')
            }
            return { ok: true, id: query.get('id'), type: query.get('type') }
        }
        return { ok: false }
    }

    const handleCarrousel = newIdx =>{
        setCurrentIdx(newIdx)
        refTitle.current.scrollIntoView({
            behavior: 'smooth',
            block: 'end'
        })
    }

    useEffect(() => {
        const { ok, id, type } = checkQueries()
        if(ok){
            getInfo(id, type).then( ({ info }) => {
                setInfo(info)
                setType(type)
            }).catch( e => {
                if(e.response.status === 400){window.location.replace('/cita')}
                if(e.response.status === 404){window.location.replace('/cita')}
            })
        }
        
    }, [])

    return (
        <MainContainer overflowHidden={currentIdx !== 1 && context.labsDate.length < 10}>
            <TitleContainer ref={refTitle}>
                <h1>CITA</h1>
            </TitleContainer>
            <FormGroup 
                dateItem={info} 
                dateType={type}
                setDateType={setType}
                currentIdx={currentIdx}
                setCurrentIdx={handleCarrousel}
            />
        </MainContainer>
    )
}

export default Date
