import React, { useState } from 'react'

import Input from './Input'
import BtnSignUp from './BtnSignUp'

import { FormContainer, FormColumn } from '../../styles/SignUp'

function Form() {

    const [name, setName] = useState('')
    const [age, setAge] = useState('')
    const [address, setAddress] = useState('')
    const [phone, setPhone] = useState('')
    const [email, setEmail] = useState('')
    const [illness, setIllness] = useState('')
    const [relativeIllness, setRelativeIllness] = useState('')
    const [emergencyContact, setEmergencyContact] = useState('')
    const [allergies, setAllergies] = useState('')
    const [bloodType, setBloodType] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')

    return (
        <FormContainer>
            <FormColumn>
                <Input 
                    label="Email: "
                    value={email}
                    setValue={setEmail}
                    type="email"
                    id="email"
                />
                <Input 
                    label="Contraseña: "
                    value={password}
                    setValue={setPassword}
                    type="password"
                    id="password"
                />
                <Input 
                    label="Confirmar contraseña: "
                    value={confirmPassword}
                    setValue={setConfirmPassword}
                    type="password"
                    id="confimrPassword"
                />
                <Input 
                    label="Nombre: "
                    value={name}
                    setValue={setName}
                    id="name"
                />
                <Input 
                    label="Edad: "
                    value={age}
                    setValue={setAge}
                    number
                    id="age"
                />
                <Input 
                    label="Dirección: "
                    value={address}
                    setValue={setAddress}
                    id="address"
                />
                <Input 
                    label="Teléfono: "
                    value={phone}
                    setValue={setPhone}
                    number
                    id="phone"
                />
            </FormColumn>
            <FormColumn>
                <Input 
                    label="Tipo de sangre: "
                    value={bloodType}
                    setValue={setBloodType}
                    id="blood"
                />
                <Input 
                    label="Enfermedades padecidas: "
                    value={illness}
                    setValue={setIllness}
                    textarea
                    id="illness"
                />
                <Input 
                    label="Padecimientos familiares: "
                    value={relativeIllness}
                    setValue={setRelativeIllness}
                    textarea
                    id="relativeIllness"
                />
                <Input 
                    label="Contacto de emergencia: "
                    value={emergencyContact}
                    setValue={setEmergencyContact}
                    number
                    id="emergencyContact"
                />
                <Input 
                    label="Alergias: "
                    value={allergies}
                    setValue={setAllergies}
                    textarea
                    id="allergies"
                />
                <BtnSignUp 
                    bloodType={bloodType}
                    name={name}
                    age={age}
                    address={address}
                    phone={phone}
                    email={email}
                    illness={illness}
                    relativeIllness={relativeIllness}
                    emergencyContact={emergencyContact}
                    allergies={allergies}
                    password={password}
                    confirmPassword={confirmPassword}
                />
            </FormColumn>
        </FormContainer>
    )
}

export default Form
