import React, { useState, useEffect, useContext } from 'react'
import axios from 'axios'

import LabsGroup from '../labs/LabsGroup'

import {  LabsContainer } from '../../styles/Labs'

import { URL } from '../../variables'
import { appContext } from '../../context/Provider'

function Labs() {

    const context = useContext(appContext)

    const [labs, setLabs] = useState([])
    const [services, setServices] = useState([])

    const getLabs = async () => {
        let res = await axios({
            method: 'GET',
            url: `${URL}/all-labs`,
            timeout: 3000
        })
        return res.data
    }

    const onItemSelect = (service, checked) => {
        let lab = labs.find( lab => lab.id == service.tipo )
        if(checked){ context.dispatchLabsDate({ type: 'ADD', value: {...service, laboratorio: lab} }) }
        else{ context.dispatchLabsDate({ type: 'REMOVE', value: service }) }
    }

    useEffect(() => {
        getLabs().then( ({labs, services}) => {
            setLabs(labs)
            setServices(services)
        }).catch( (e) => {
            alert('Ha sucedido un error en el servidor. Intentalo más tarde.')
        })
    }, [])

    return (
        <LabsContainer paddingTop="0" width="80%">
            {labs.map( (lab,idx) => (
                <LabsGroup 
                    lab={lab} 
                    services={services.filter( service => service.tipo === lab.id )} 
                    closed={idx!==0}
                    key={lab.id}
                    itemWidth="70vw"
                    onItemSelect={onItemSelect}
                    wCheckbox
                />
            ) )}
        </LabsContainer>
    )
}

export default Labs
