import React, { useState } from 'react'

import { LabsServiceContainer, LabsServiceCheckboxContainer, Checkbox, LabsServiceText, LabsServiceLink } from '../../styles/Labs'

import { URL } from '../../variables'

function LabServiceItem({ idx, service, checkbox, onClick }) {

    const [checked, setChecked] = useState(false)

    const handleCheckbox = () => {
        onClick(service, !checked)
        setChecked(!checked)
    }

    return (
        <LabsServiceContainer bgColor={ idx%2 !== 0 ? 'rgb(235,235,235)' : 'rgb(245,245,245)'}>
            <LabsServiceCheckboxContainer 
                pLeft={checkbox ? '3vw' : '8vw'}
                mRight={checkbox ? '10vw' : '5vw'}
            >
                {checkbox && (
                    <Checkbox onClick={handleCheckbox} bgColor={!checked ? 'white': 'rgb(0,47,91)'}>
                    </Checkbox>
                )}
                <p>{service.nombre}</p>
            </LabsServiceCheckboxContainer>
            <LabsServiceText width={checkbox ? '17vw' : '10vw'}>${service.precio.toFixed(2)}</LabsServiceText>
            <LabsServiceText width="18vw">${service.olprecio.toFixed(2)} Pago en línea</LabsServiceText>
            {!checkbox && <LabsServiceLink href={`${URL}/laboratorios/${service.id}`}>Ver datalles...</LabsServiceLink>}
        </LabsServiceContainer>
    )
}

export default LabServiceItem
