import React from 'react'
import { faStar } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { ReviewStars, StarContainer } from '../styles/Info'

const Star = ({ value, idx, setValue,  }) => {

    const handleStar = () => setValue(idx+1)

    return(
        <StarContainer onClick={handleStar}>
            <FontAwesomeIcon 
                icon={faStar} 
                color={idx < value ? 'rgb(255,196,2)' : 'rgb(150,150,150)'} 
                size="1x" 
                className="star-icon"
            />
        </StarContainer>
    )
}

function Stars({ canSelect, value, setValue, mgBottom }) {

    const starsAray = ['', '', '', '', '']

    return (
        <ReviewStars mgBottom={mgBottom}> 
            {starsAray.map( (star, idx) => (
                canSelect ? (
                    <Star 
                        value={value}
                        idx={idx}
                        setValue={setValue}
                        key={idx}
                    />
                ):(
                    <FontAwesomeIcon 
                        icon={faStar} 
                        color={idx < value ? 'rgb(255,196,2)' : 'rgb(150,150,150)'} 
                        size="1x" 
                        key={idx}
                        className="star-icon"
                    />
                )
                
            ))}
        </ReviewStars>
    )
}

export default Stars
