import React, { useState } from 'react'
import axios from 'axios'
import Loader from 'react-loader-spinner'

import { BtnFinishDate, ErrMessage } from '../../styles/Date'
import { InputContainer, MessageSuccess } from '../../styles/SignUp'

import { URL } from '../../variables'

function BtnSignUp({ name, age, address, phone, email, illness, relativeIllness, emergencyContact, allergies, bloodType, password, confirmPassword}) {
    
    const [err, setErr] = useState(false)
    const [success, setSuccess] = useState(false)
    const [loading, setLoading] = useState(false)
    const [message, setMessage] = useState('')

    const createUser = async (data) => {
        const res = await axios({
            url: `${URL}/user`,
            method: 'POST',
            data
        })

        return res.data
    }

    const handleBtn = () => {
        if(checkData() && !loading){
            setLoading(true)
            setSuccess(false)
            setErr(false)
            const data = { name, age, address, phone, email, illness, relativeIllness, emergencyContact, allergies, bloodType: bloodType.value, password }
            createUser(data).then( ({ ok, message }) => {
                setLoading(false)
                if(!ok){
                    setErr(true)
                    setMessage(message)
                    return
                }
                setSuccess(true)
            }).catch((e) => {
                setLoading(false)
                if(e.response.status === 400){
                    setMessage('DEBES COMPLETAR TODOS LOS CAMPOS')
                    setErr(true)
                    return
                }
                setMessage('HA OCURRIDO UN ERROR EN EL SERVIDOR. VUELVE A INTENTARLO MÁS TARDE')
                setErr(true)
                setSuccess(false)
            })
        }
    }

    const checkData = () => {
        if(name === "" || age === "" || address === "" || phone === "" || email === "" || illness === "" || relativeIllness === "" ||
        emergencyContact === "" || allergies === "" || password === "" || confirmPassword === ""){
            setMessage('DEBES COMPLETAR TODOS LOS CAMPOS')
            setErr(true)
            return false
        }
        if(password !== confirmPassword){
            console.log(password)
            console.log(confirmPassword)
            setMessage('LAS CONTRASEÑAS DEBEN COINCIDIR')
            setErr(true)
            return false
        }
        setMessage('')
        setErr(false)
        return true
    }

    return (
        <InputContainer>
            {(err || success ||loading) ? (
                err ? <ErrMessage color='red' width="60%" mTop="0">{message}</ErrMessage> :
                success ? <MessageSuccess>SE HA ENVIADO UN CORREO DE CONFIRMACIÓN A TU EMAIL.</MessageSuccess> :
                <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>
            ) : <div></div>}
            <BtnFinishDate onClick={handleBtn}>CREAR CUENTA</BtnFinishDate>
        </InputContainer>
    )
}

export default BtnSignUp
