import { createGlobalStyle } from 'styled-components'

const GlobalStyle = createGlobalStyle`

    .react-calendar {
        width: 30vw;
        max-width: 100%;
        background: white;
        font-family: Montserrat;
        line-height: 1.125em;
        border: 0;
        height: 60%;
    }

    .react-calendar__navigation button span{
        min-width: 44px;
        background: none;
        font-family: Montserrat;
        font-size: 1vw;
    }

    .react-calendar button{
        height: 7vh;
        border: 2px solid rgb(230,230,230);
    }

    .react-calendar abbr{
        text-decoration: none;
    }

    .react-calendar__month-view__days__day--weekend {
        color: black;
    }
    .react-calendar__month-view__days__day--neighboringMonth {
        color: #757575;
    }

    .react-calendar__tile{
        background-color: rgb(240,240,240);
    }

    .react-calendar__tile--active {
        background: rgb(0,47,91);
        color: rgb(235,235,235);
    }

    .react-calendar__tile--active:enabled:focus {
        background: rgb(0,47,91);  
    }    

    .rc-time-picker-input{
        background-color: rgb(230,230,230);
        font-family: Montserrat;
        font-size: 1.2vw;
        width: 5vw;
        text-align: center;
    }

    .rc-time-picker-clear{ display: none !important; }

    .rc-time-picker-panel-select li{ font-family: Montserrat; }

    .rc-time-picker-panel-input{ font-family: Montserrat; }
    
`

export default GlobalStyle