import React from 'react'

import { MenuContainer } from '../../styles/User'

function Menu({ edit, logout }) {
    return (
        <MenuContainer>
            <h2>Menú</h2>
            <ul>
                <li onClick={edit}>Editar</li>
                <li>Laboratorios</li>
                <li>Recetas</li>
                <li onClick={logout}>Cerrar sesión</li>
            </ul>
        </MenuContainer>
    )
}

export default Menu
