import React, { useContext, useState } from 'react'
import axios from 'axios'

import { BtnFinishDate, ErrMessage } from '../../styles/Date'

import { URL } from '../../variables'
import { appContext } from '../../context/Provider'

function BtnDate({ name, age, illness, phone, email, address, homeService }) {

    const context = useContext(appContext)

    const [err, setErr] = useState(false)
    const [success, setSuccess] = useState(false)
    const [errMessage, setErrMessage] = useState('')

    const postDate = async (data) => {
        const res = await axios({
            url: `${URL}/cita`,
            method: 'POST',
            data
        })

        return res.data
    }

    const handleBtn = () => {
        if(checkData()){
            let data = {
                date: context.date, type: context.dateType,
                name, age: Number(age), illness, phone, email, address, homeService,
            }

            if(context.dateType === "medico"){ data.doctor = context.doctorDate.id }
            else{ data.labs = context.labsDate.map( item => item.id) }

            postDate(data).then(() => {
                setErrMessage('CITA AGENDADA. ESPERE LA CONFIRMACIÓN EN SU CORREO')
                setSuccess(true)
            }).catch((e) => {
                if(e.response.status === 400){
                    setErrMessage('DEBES COMPLETAR TODOS LOS CAMPOS')
                    setErr(true)
                    return
                }
                setErrMessage('HA OCURRIDO UN ERROR EN EL SERVIDOR. VUELVE A INTENTARLO MÁS TARDE')
                setErr(true)
            })
        }
    }

    const checkData = () => {
        if(name === "" || age === "" || illness === "" || phone === "" || email === "" || (homeService && address === "") ||
        (JSON.stringify(context.doctorDate) === "{}" && context.labsDate.length === 0)){
            setErrMessage('DEBES COMPLETAR TODOS LOS CAMPOS')
            setErr(true)
            return false
        }
        setErrMessage('')
        setErr(false)
        return true
    }

    return (
        <>
            <BtnFinishDate onClick={handleBtn}>HACER CITA</BtnFinishDate>
            {(err || success) && <ErrMessage color={err ? 'red' : 'rgb(0,47,91)'}>{errMessage}</ErrMessage>}
        </>
    )
}

export default BtnDate
