import React, { useEffect, useState } from 'react'
import axios from 'axios'

import Navbar from '../components/Navbar'
import InfoContainer from '../components/info/InfoContainer'
import Reviews from '../components/info/Reviews'

import { MainContainer } from '../styles/Info'

import { URL } from '../variables'

function Info() {

    const infoType = window.location.pathname.split('/')[ window.location.pathname.split('/').length - 2 ] === 'medicos' ? 'medico' : 'laboratorio'

    const [info, setInfo] = useState({})
    const [reviews, setReviews] = useState([])

    const getInfo = async (id) => {
        let res = await axios({
            url: `${URL}/${infoType}/${id}?reviews=true`,
            method: 'GET',
            timeout: 5000
        })
        return res.data
    }

    useEffect(() => {
        let id = window.location.pathname.split('/')[ window.location.pathname.split('/').length - 1 ]
        getInfo(id).then( ({ info, reviews }) => {
            setInfo(info)
            setReviews(reviews)
        }).catch( e => {
            if(e.response.status === 400){ 
                if(infoType === "medico"){ window.location.replace('/medicos')  }
                else{ window.location.replace('/laboratios')  }
            }
        })
    }, [])

    return (
        <MainContainer>
            <Navbar bgNavbar={true}/>
            <InfoContainer info={info} infoType={infoType}>
                <Reviews 
                    reviews={reviews} 
                    itemID={info.id}
                    infoType={infoType}
                />
            </InfoContainer>
        </MainContainer>
    )
}

export default Info
