import React from 'react'

import { DefaultImage, InfoContainer, InfoItem, Image } from '../../styles/User'

import { ReactComponent as UserIcon } from '../../assets/icons/usuario_imagen_default.svg';

function Info({ user }) {

    return (
        <>
            {user.imagen ? (
                <Image src={user.imagen}/>
            ):(
                <DefaultImage>
                    <UserIcon width="10vw" height="10vw"/>
                </DefaultImage>
            )}
            <InfoContainer>
                <InfoItem>
                    <span>NOMBRE:</span>
                    <p>{user.nombre}</p>
                </InfoItem>
                <InfoItem>
                    <span>EDAD:</span>
                    <p>{user.edad}</p>
                </InfoItem>
                <InfoItem>
                    <span>TIPO DE SANGRE:</span>
                    <p>{user.sangre}</p>
                </InfoItem>
                <InfoItem>
                    <span>DIRECCIÓN:</span>
                    <p>{user.direccion}</p>
                </InfoItem>
                <InfoItem>
                    <span>TELÉFONO:</span>
                    <p>{user.telefono}</p>
                </InfoItem>
                <InfoItem>
                    <span>EMAIL:</span>
                    <p>{user.email}</p>
                </InfoItem>
                <InfoItem>
                    <span>ENFERMEDADES:</span>
                    <p>{user.enfermedades}</p>
                </InfoItem>
                <InfoItem>
                    <span>ENFERMEDADES FAMILIARES:</span>
                    <p>{user.enfermedadesFam}</p>
                </InfoItem>
                <InfoItem>
                    <span>CONTACTO:</span>
                    <p>{user.contacto}</p>
                </InfoItem>
                <InfoItem mgBottom="3vw">
                    <span>ALERGIAS:</span>
                    <p>{user.alergias}</p>
                </InfoItem>
            </InfoContainer>
        </>
    )
}

export default Info
