import React from 'react'

import { TopBarContainer, TopBarText, LoginLabel, PhoneContainer } from '../../styles/Home'

import { ReactComponent as LogoPhone } from '../../assets/icons/phone.svg';

import { URL } from '../../variables'

function TopBar() {
    return (
        <TopBarContainer>
            <PhoneContainer>
                <LogoPhone />
                <TopBarText>ATENCIÓN 24 HRS (871) 480 2907</TopBarText>
            </PhoneContainer>
            <LoginLabel href={`${URL}#login`}>ACCESO CLIENTE</LoginLabel>
        </TopBarContainer>
    )
}

export default TopBar
