import React, { useState, useEffect } from 'react'

import LabServiceItem from './LabServiceItem'
import Stars from '../Stars'

import { LabsGroupContainer, LabNameContainer, LabDescription, LabSeeMore, LabsServicesContainer, LabImageContainer } from '../../styles/Labs'

import { ReactComponent as HemIcon } from '../../assets/icons/hematologia.svg';
import { ReactComponent as CoaIcon } from '../../assets/icons/coagulacion.svg';
import { ReactComponent as QuSaIcon } from '../../assets/icons/quimica-sanguinea.svg';

function LabsGroup({ lab, services, closed, itemWidth, onItemSelect, wCheckbox }) {

    const [openServices, setOpenServices] = useState(true)

    const handleOpenServices = () => setOpenServices(!openServices)

    const getImage = () => {
        switch(lab?.nombre){
            case "HEMATOLOGÍA": return( <HemIcon className="icon-lab"/> )
            case "COAGULACIÓN": return( <CoaIcon className="icon-lab"/> )
            case "QUÍMICA SANGUINEA": return( <QuSaIcon className="icon-lab"/> )
            default:
                break;
        }
    }

    useEffect(() => {
        if(closed){ setOpenServices(false) } 
    }, [closed])

    return (
        <>
            <LabsGroupContainer width={itemWidth}>
                <LabImageContainer>
                    {getImage()}
                </LabImageContainer>
                <div>
                    <LabNameContainer>
                        <h2>{lab?.nombre}</h2>
                        <Stars value={Math.floor(lab.estrellas)}/>
                    </LabNameContainer>
                    <LabDescription>
                        {!wCheckbox && (
                            <>
                            {lab.descripcion}
                            <br/>
                            </>
                        )}
                        <LabSeeMore onClick={handleOpenServices}>{openServices ? 'ESTUDIOS:' : 'VER ESTUDIOS'}</LabSeeMore>
                    </LabDescription>
                </div>
            </LabsGroupContainer>
            <LabsServicesContainer 
                height={openServices ? `${services.length * 2}vw` : '0'}
                width={itemWidth}
            >
                {services.map( (service, idx) => (
                    <LabServiceItem 
                        service={service}
                        idx={idx}
                        key={service.id}
                        checkbox={wCheckbox}
                        onClick={onItemSelect}
                    />
                ) )}
            </LabsServicesContainer>
        </>
    )
}

export default LabsGroup
