import React, { useState, useEffect, useRef } from 'react'
import axios from 'axios';
import Loader from 'react-loader-spinner'

import { ContainerForm, InfoContainer, EditBtn, BtnContainer, Message, LoaderContainer, InfoItem, InputImage, Image } from '../../styles/User'

import { ReactComponent as UserIcon } from '../../assets/icons/usuario_imagen_default.svg';

import { URL as URL_SERVER } from '../../variables'

function Form({ cancelEdit, user }) {

    const refInputFile = useRef(null)

    const [name, setName] = useState('')
    const [age, setAge] = useState('')
    const [address, setAddress] = useState('')
    const [phone, setPhone] = useState('')
    const [contact, setContact] = useState('')
    const [message, setMessage] = useState('')
    const [imageURL, setImageURL] = useState('')
    const [image, setImage] = useState(null)
    const [err, setErr] = useState(false)
    const [loading, setLoading] = useState(false)

    const handleName = e => setName(e.target.value)
    const handleAge = e => setAge(e.target.value.replace(/[^0-9]/g, ''))
    const handleAddress = e => setAddress(e.target.value)
    const handlePhone = e => setPhone(e.target.value.replace(/[^0-9]/g, ''))
    const handleContact = e => setContact(e.target.value.replace(/[^0-9]/g, ''))

    const saveChanges = async (data) => {
        const res = await axios({
            url: `${URL_SERVER}/user/${user.id}`,
            data,
            method: 'PUT',
            headers: { 'Content-Type': 'multipart/form-data' }
        })

        return res.data
    }

    const handleBtn = (e) => {
        e.preventDefault()
        if(checkData() && !loading){
            setLoading(true)
            setErr(false)
            const data = getFormData({ name, age, address, phone, contact })
            console.log(data)
            saveChanges(data).then(() => {
                setLoading(false)
                window.location.reload()
            }).catch(e => {
                console.log(e)
                setErr(true)
                setLoading(false)
                if(e.response.status === 400){ setMessage('NO PUEDEN HABER CAMPOS EN BLANCO') }
                setMessage('HA OCURRIDO UN ERROR EN EL SERVIDOR INTENTALO MÁS TARDE')
            })
        }else{
            setErr(true)
            setMessage('NO PUEDEN HABER CAMPOS EN BLANCO')
        }
    }

    const getFormData = (object) => {
        let formData = new FormData();
        if(image){ formData.append('image', image) }
        Object.keys(object).forEach(key => formData.append(key, object[key]));
        return formData;
    }

    const checkData = () => {
        if(name === "" || age === "" || address === "" || phone === "" || contact === "") { return false }
        return true
    }

    const handleUploadImage = () => {
        refInputFile.current.click()
    }

    const handleInputImage = e => { 
        console.log(e.target.files[0])
        if(e.target.files[0].size > 5000000){
            setErr(true)
            return setMessage('LA IMAGEN DEBE PESAR MENOS DE 5MB')
        }
        if(image){ URL.revokeObjectURL(imageURL) }
        setImage(e.target.files[0])
        setImageURL(URL.createObjectURL(e.target.files[0]))
        setErr(false)
    }

    useEffect(() => {
        setName(user.nombre)
        setAddress(user.direccion)
        setAge(user.edad)
        setPhone(user.telefono)
        setContact(user.contacto)
    }, [user])

    return (
        <ContainerForm>
            {(user.imagen || image) ? (
                <Image src={user.imagen || imageURL}/>
            ):(
                <InputImage onClick={handleUploadImage}>
                    <UserIcon width="10vw" height="10vw"/>
                </InputImage>
            )}
            <InfoContainer width="68%">
                <InfoItem>
                    <label htmlFor="name">NOMBRE:</label>
                    <input 
                        id="name"
                        value={name}
                        onChange={handleName}
                    />
                </InfoItem>
                <InfoItem>
                    <label htmlFor="age">EDAD:</label>
                    <input 
                        id="age"
                        value={age}
                        onChange={handleAge}
                    />
                </InfoItem>
                <InfoItem>
                    <label htmlFor="address">DIRECCIÓN:</label>
                    <input 
                        id="address"
                        value={address}
                        onChange={handleAddress}
                    />
                </InfoItem>
                <InfoItem>
                    <label htmlFor="phone">TELÉFONO:</label>
                    <input 
                        id="phone"
                        value={phone}
                        onChange={handlePhone}
                    />
                </InfoItem>
                <InfoItem>
                    <label htmlFor="contact">CONTACTO:</label>
                    <input 
                        id="contact"
                        value={contact}
                        onChange={handleContact}
                    />
                </InfoItem>
                <input 
                    style={{ display: 'none' }}
                    type="file"
                    ref={refInputFile}
                    accept="image/*"
                    name="image"
                    onChange={handleInputImage}
                />
                <BtnContainer>
                    <EditBtn onClick={cancelEdit}>CANCELAR</EditBtn>
                    <EditBtn onClick={handleBtn}>GUARDAR</EditBtn>
                </BtnContainer>
                {err && <Message>{message}</Message>}
                {loading && (
                    <LoaderContainer>
                        <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>
                    </LoaderContainer>
                )}
                
            </InfoContainer>
        </ContainerForm>
    )
}

export default Form
