import React from 'react'

import LoginForm from '../components/home/LoginForm'

import { MainContainer } from '../styles/AdminLogin'
import { TitleHomeContainer, TitleHome } from '../styles/Home'

import { ReactComponent as LogoIcon } from '../assets/icons/logo-white.svg';

function AdminLogin() {
    return (
        <MainContainer>
            <TitleHomeContainer>
                <LogoIcon/>
                <TitleHome color='white' fontSize="6vw">M</TitleHome>
                <TitleHome color='rgb(178,223,252)' fontSize="6vw">D</TitleHome>
                <TitleHome color='white' fontSize="4vw" marginLeft="1vw">MÉDICA</TitleHome>
            </TitleHomeContainer>
            <LoginForm
                height="fit-content"
                admin
            />
        </MainContainer>
    )
}

export default AdminLogin
