import React, { useEffect, useState } from 'react'
import axios from 'axios'

import Navbar from '../components/Navbar'
import Form from '../components/signup/Form'
import Confirmation from '../components/signup/Confirmation'

import { MainContainer, ContentContainer, TitleContainer } from '../styles/Date'

import { URL } from '../variables'

function SignUp({ location }) {

    const [isAuthentication, setIsAuthentication] = useState(false)
    const [authenticationSucceeded, setAuthenticationSucceeded] = useState(false)
    const [message, setMessage] = useState('')

    const validToken = async (token) => {
        const res = await axios({
            url: `${URL}/user/verify`,
            method: 'GET',
            headers:{
                'Authorization': `Barer ${token}`
            }
        })
        return res.data
    }

    useEffect(() => {
        const query = new URLSearchParams(location.search);
        const token = query.get('token')
        if(token){
            setIsAuthentication(true)
            validToken(token).then(({ ok }) => {
                if(ok){ 
                    setMessage('SE HA AUTENTICADO EL EMAIL')
                    return setAuthenticationSucceeded(true) 
                }
                setAuthenticationSucceeded(false)
                setMessage('HA HABIDO UN PROBLEMA CON LA AUTENTICACIÓN DEBIDO A QUE EL TOKEN HA CADUCADO O NO ES VALIDO')
            }).catch((e) => {
                console.log(e)
                setMessage('HA HABIDO UN PROBLEMA CON LA AUTENTICACIÓN DEBIDO A UN ERROR EN EL SERVIDOR')
                setAuthenticationSucceeded(false)
            })
        }
    }, [])

    return (
        <MainContainer>
            <Navbar bgNavbar={true}/>
            <ContentContainer>
                <TitleContainer>
                    <h1>REGISTRO</h1>
                </TitleContainer>
                {isAuthentication ? (
                    authenticationSucceeded ? <Confirmation message={message} color="rgb(0,47,91)" /> : 
                    <Confirmation message={message} color="red"/>
                ):<Form/>}
            </ContentContainer>
        </MainContainer>
    )
}

export default SignUp
