import React, { useState, useEffect } from 'react'

import { Input } from '../../styles/Date'

function DoctorsFilter({ doctors, setDoctors }) {

    const [filter, setFilter] = useState('')

    const handleInput = e => setFilter(e.target.value)

    const removeAccents = str => {
        let newStr = str
        newStr = newStr.replace(/á/g, 'a')
        newStr = newStr.replace(/Á/g, 'A')
        newStr = newStr.replace(/í/g, 'i')
        newStr = newStr.replace(/Í/g, 'I')
        newStr = newStr.replace(/ó/g, 'o')
        newStr = newStr.replace(/Ó/g, 'O')
        newStr = newStr.replace(/ú/g, 'u')
        newStr = newStr.replace(/Ú/g, 'U')
        newStr = newStr.replace(/é/g, 'e')
        newStr = newStr.replace(/É/g, 'E')
        return newStr
    }

    useEffect(() => {
        if(filter.length > 2){
            let doctorsFilter = doctors.filter( doctor => {
                const strNombre = removeAccents(doctor.nombre)
                const strFilter = removeAccents(filter)

                return strNombre.match( new RegExp(`${strFilter}`, 'gi') )
            })
            setDoctors(doctorsFilter)
        }else{ setDoctors(doctors) }
    }, [filter])

    return (
        <Input 
            value={filter}
            onChange={handleInput}
            placeholder="BUSCAR DOCTORES"
            mgBottom="1vw"
        />
    )
}

export default DoctorsFilter
