import React, { useState, useEffect, useRef } from 'react'
import Cookies from 'js-cookie';

import TopBar from '../components/home/TopBar'
import Navbar from '../components/Navbar'
import Info from '../components/home/Info'
import Services from '../components/home/Services'
import LoginForm from '../components/home/LoginForm'
import CarouselImages from '../components/home/CarouselImages'

import { MainContainer } from '../styles/Home'

const img1 = require('../assets/img/img1.jpg')
const img2 = require('../assets/img/img2.jpg')
const img3 = require('../assets/img/img3.jpg')

function Home() {

    const homeRef = useRef(null)

    const [imgs, setImgs] = useState([img1, img2, img3])
    const [bgNavbar, setBgNavbar] = useState(false)
    const [isLogged, setIsLogged] = useState(false)

    const handleScroll = () => {
        if(homeRef.current.scrollTop > window.innerHeight * 0.05){ setBgNavbar(true) }
        else{ setBgNavbar(false) }
    }

    useEffect(() => {
        homeRef.current.addEventListener('scroll', handleScroll)
        if(Cookies.get('payload')){ setIsLogged(true) }
        return () => {
            homeRef.current.removeEventListener('scroll', handleScroll)
        }
    }, [])

    return (
        <MainContainer ref={homeRef}>
            <TopBar/>
            <Navbar bgNavbar={bgNavbar} isLogged={isLogged}/>
            <CarouselImages imgs={imgs}/>
            <Info />
            <Services />
            <LoginForm isLogged={isLogged}/>
        </MainContainer>
    )
}

export default Home
