import styled from 'styled-components'

const darkBlue = "rgb(0,47,91)"
const lightGray = "rgb(235,235,235)"

export const MainContainer = styled.div`
    width: 100%;
    min-height: 100%;
    overflow-x: hidden;
    overflow-y: ${props => props.overflowHidden ? 'hidden' : 'auto'}; 
`

export const ContentContainer = styled.div`
    width: 100%;
    padding-top: 10vw;
    display: flex;
    align-items: center;
    flex-direction: column;
`

export const TitleContainer = styled.div`
    width: 76vw;
    height: 5vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 2vw;
    border-bottom: 0.2vw solid ${darkBlue};
    margin-bottom: 2vw;

    h1{
        font-family: Trajan;
        color: ${darkBlue};
        font-size: 2vw;
        margin: 0;
    }
`

export const FormGroupContainer = styled.div`
    width: 100vw;
    height: 60vh;
`
export const FormGroupItemsContainer = styled.div`
    width: 100vw;
    height: 60vh;
    display: flex;
    flex-direction: row;
`

export const GroupContainer = styled.div`
    width: 70vw;
    height: 57vh;
    display: flex;
    flex-direction: ${ props => props.fDirection || 'row' };
    justify-content: space-between;
    padding:  0 15vw;
    flex-shrink: 0;
    transform: ${props => `translateX(${props.translateX}vw)`};
    transition: transform 500ms ease-in;
    align-items: ${props => props.alignItems || 'flex-start'};
`

export const FormContainer = styled.div`
    width: 50vw;
`

export const InputContainer = styled.div`
    width: 50vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: ${ props => props.mgBottom || '1vw' };

    div{
        display: flex;
        flex-direction: row;
        align-items: center;
    }
`

export const InputLabel = styled.label`
    font-family: MontserratMedium;
    color: ${darkBlue};
    font-size: 1.5vw;
    width: 15vw;
`

export const Input = styled.input`
    background-color: ${ props => props.bgColor || lightGray};
    width: 35vw;
    border-radius: 0.3rem;
    padding: 0 1vw;
    height: 3vw;
    border: 0;
    font-family: Montserrat;
    font-size: 1vw;
    margin-bottom: ${ props => props.mgBottom || '0'};
    &:focus{ outline: 0 }
`

export const CheckboxInputContainer = styled.div`
    background-color: ${ props => props.bgColor || lightGray};
    width: 35vw;
    border-radius: 0.3rem;
    padding: 0;
    padding-left: 1vw;
    height: 3vw;
    border: 0;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;

    input{
        background-color: ${lightGray};
        width: 70%;
        height: 90%;
        border: 0;
        font-family: Montserrat;
        font-size: 1vw;
        &:focus{ outline: 0 }
    }

    div.checkbox-container{
        min-width: 35%
    }
`

export const TextArea = styled.textarea`
    background-color: ${lightGray};
    width: 35vw;
    border-radius: 0.3rem;
    padding: 1vw;
    height: 5vw;
    border: 0;
    font-family: Montserrat;
    resize: none;
    font-size: 1vw;
    &:focus{ outline: 0 }
`

export const DateItemContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const DateItemImage = styled.img`
    width: 12vw;
    height: 12vw;
    border-radius: 0.5rem;
    border: solid 1px ${darkBlue};
    margin-bottom: 0.5vw;
`

export const DateItemName = styled.h1`
    font-family: MontserratMedium;
    font-size: 1.2vw;
    margin: 0;
    margin-bottom: 0.3vw;
`

export const DateItemText = styled.h2`
    font-family: MontserratLight;
    font-size: 1.2vw;
    margin: 0;
    margin-bottom: 0.3vw;
`

export const BtnsCarrouselContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: ${props => props.justifyContent};
    width: 80vw;
    margin: 0 10vw;
`

export const BtnCarrousel = styled.button`
    background-color: ${darkBlue};
    width: 2vw;
    height: 2vw;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:0;
    border: 0;
    color: white;
    font-size: 0.7vw;
    position: relative;
    bottom: calc(30vh + 1vw);
    z-index: 1;
    &:hover{ cursor: pointer; }
`

export const ItemsContainer = styled.div`
    width: 70vw;
    height: 57vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
`

export const DateFormContainer = styled.div`
    width: 30vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const CalendarContainer = styled.div`
    width: 30vw;
    display: flex;
    flex-direction: column;
    border: 1px solid rgb(240,240,240);
    min-height: 40vh;
    flex-shrink: 0;
`

export const DateMonth = styled.span`
    font-family: Montserrat;
    font-size: 2vw;
    margin: 1vw 0;
`

export const CalendarNavigation = styled.div`
    width: 90%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 5%;
    min-height: 4.5vw;
`

export const CalendarNavigationBtnContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
`

export const CalendarNavigationBtn = styled.button`
    background-color: transparent;
    border: 0;
    &:hover{ cursor: pointer; }
`

export const CalendarHourContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 1vw;
`

export const CalendarHour = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    span{
        font-family: Montserrat;
        font-size: 1.2vw;
    }
`

export const TypeDateContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(230,230,230);
    margin-left: 1vw;
    border-radius: 0.3rem;
    padding: 0.5vw;
    span:first-child{ margin-bottom: 0.5vw; }
    &:hover{
        cursor: pointer;
    }
`

export const TypeDate = styled.span`
    width: 9vw;
    text-align: center;
    font-family: Montserrat;
    padding: 0.2vw 0;
    border-radius: 0.3rem;
    background-color: ${props => props.doctorType ? `${darkBlue}` : 'rgb(240,240,240)'};
    color: ${props => props.doctorType ? `white` : 'black'};
    font-size: 1vw;
`

export const IconLabContainer = styled.div`
    width: 12vw;
    border-radius: 0.5rem;
    border: solid 1px ${darkBlue};
    margin-bottom: 0.5vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    padding: 1vw 0;
`

export const BtnFinishDate = styled.button`
    padding: 0.5vw 1vw;
    border-radius: 0.5rem;
    background-color: ${darkBlue};
    font-family: MontserratMedium;
    border: 0;
    font-size: 1vw;
    color: white;
    &:hover{ cursor: pointer; }
`

export const ErrMessage = styled.span`
    color: ${props => props.color};
    font-family: MontserratMedium;
    font-size: 1vw;
    text-align: center;
    margin-top: ${props => props.mTop || '1vw'};
    width: ${props => props.width || '12vw'};
`