import styled from 'styled-components'

const purple = "rgb(88,32, 142)"
const darkBlue = "rgb(0,47,91)"
//const lightBlue = "rgb(178,223,252)"

export const LabsContainer = styled.div`
    width: 100%;
    padding-top: ${ props => props.paddingTop || '10vw' };
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-bottom: 5vw;
`

export const LabsGroupContainer = styled.div`
    width: ${ props => props.width || '80vw' };
    padding: 1vw 0;
    height: fit-content;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-shrink: 0;
`

export const LabNameContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;

    h2{
        font-family: MontserratMedium;
        color: ${darkBlue};
        font-size: 1.5vw;
        margin: 0;
        margin-right: 1vw;
    }
`
export const LabDescription = styled.p`
    margin: 0;
    font-family: Montserrat;
    color: rgb(150,150,150);
    font-size: 0.9vw;
    text-align: justify;
`

export const LabSeeMore = styled.span`
    margin: 0;
    font-family: Montserrat;
    color: ${purple};
    font-size: 0.9vw;
    margin-top: 0.2vw;
    &:hover{ cursor: pointer; }
`

export const LabsServicesContainer = styled.div`
    width: ${ props => props.width || '80vw' };;
    transition: height 500ms ease-in-out;
    height: ${props => props.height};
    overflow-y: hidden;
    flex-shrink: 0;
`

export const LabImageContainer = styled.div`
    width: 7vw;
    height: 7vw;
    margin-right: 1vw;
`

export const LabsServiceContainer = styled.div`
    width: 70vw;
    background-color: ${props => props.bgColor};
    height: 2vw;
    display: flex;
    flex-direction: row;
    align-items: center;
`

export const LabsServiceCheckboxContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: ${ props => props.pLeft};
    width: 15vw;
    margin-right: ${ props => props.mRight};
    p{
        font-family: Montserrat;
        font-size: 0.9vw;
    }
`

export const Checkbox = styled.div`
    width: 1vw;
    height: 1vw;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.1vw;
    margin-right: 1vw;
    background-color: ${props => props.bgColor};
    &:hover{ cursor: pointer; }
    
`

export const LabsServiceText = styled.p`
    font-family: Montserrat;
    font-size: 0.9vw;
    width: ${props => props.width};
`

export const LabsServiceLink = styled.a`
    font-family: Montserrat;
    font-size: 0.9vw;
    color: ${darkBlue};
    text-decoration: none;
`