import styled from 'styled-components'

const darkBlue = "rgb(0,47,91)"

export const FormChangePassword = styled.form`
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;

    input{
        width: 40%;
        font-family: Montserrat;
        font-size: 1vw;
        padding: 0.5vw 2vw;
        background-color: rgb(240,240,240);
        border-radius: 0.3rem;
        border: 0;
        height: 1.5vw;
        margin-bottom: 2vw;
        &:focus{ outline: 0; }
    }

    button{
        background-color: ${darkBlue};
        font-family: MontserratBold;
        font-size: 1.3vw;
        color: white;
        border-radius: 0.3rem;
        padding: 0.5vw 2vw;
        border: 0;
        margin-bottom: 2vw;
        &:hover{ cursor: pointer; }
    }

    span{
        color: red;
        font-family: MontserratBold;
        font-size: 1.5vw;
    }
`