import React, { useEffect, useState } from 'react'
import axios from 'axios'

import MainContainer from '../components/MainContainer'
import Confirmation from '../components/signup/Confirmation'
import Form from '../components/forgotPassword/Form'

import { URL } from '../variables'

function ForgotPassword({ location }) {

    const [authenticated, setAuthenticated] = useState(false)
    const [message, setMessage] = useState('')
    const [token, setToken] = useState('')

    const validToken = async (token) => {
        const res = await axios({
            url: `${URL}/user/valid`,
            method: 'GET',
            headers:{
                'Authorization': `Barer ${token}`
            }
        })
        return res.data
    }

    useEffect(() => {
        const query = new URLSearchParams(location.search);
        const token = query.get('token')
        if(token){
            validToken(token).then(() => {
                setAuthenticated(true)
                setToken(token)
            }).catch((e) => {
                console.log(e)
                if(e.response.status === 400){ window.location.replace('/') }
                setMessage('HA HABIDO UN PROBLEMA CON LA AUTENTICACIÓN DEBIDO A UN ERROR EN EL SERVIDOR')
            })
        }else{ location.replace('/') }
    }, [])

    return (
        <MainContainer title="CAMBIAR CONTRASEÑA">
            {authenticated && <Form token={token}/> }
            {message && <Confirmation color="red" message={message}/>}
        </MainContainer>
    )
}

export default ForgotPassword
