import React, { useEffect, useState } from 'react'
import axios from 'axios'

import { DiscountAnnouncement, DiscounntText, DiscounntTextBold, LineSeparator } from '../../styles/Home'

import { URL }  from '../../variables'

function PromInfo({ width, sizeTBold, sizeTNormal, lineWidth }) {

    const [text1, setText1] = useState('')
    const [text2, setText2] = useState('')
    const [text3, setText3] = useState('')

    const getInfo = async () => {
        const res = await axios({
            url: `${URL}/api?disccountInfo=true`,
            method: 'GET'
        })

        return res.data
    }

    useEffect(() => {
        getInfo().then(({ texto1, texto2, texto3 }) => {
            setText1(texto1)
            setText2(texto2)
            setText3(texto3)
        }).catch((e) => {
            console.log(e)
            setText1('DESCUENTOS')
            setText2('EN SERVICIOS SELECCIONADOS')
            setText3('PAGANDO AQUÍ')
        })
    }, [])

    return (
        <DiscountAnnouncement
            width={width}
        >
            <DiscounntTextBold color="rgb(178,223,252)" size={`${sizeTBold}vw`}>{text1}</DiscounntTextBold>
            <DiscounntText size={sizeTNormal}>{text2}</DiscounntText>
            <DiscounntTextBold size={`${sizeTBold+1}vw`}>{text3}</DiscounntTextBold>
            <LineSeparator
                color="rgb(178,223,252)"
                height="0.7vw"
                lineWidth={lineWidth}
            />
        </DiscountAnnouncement>
    )
}

export default PromInfo
