import React, { useState } from 'react'
import { CSSTransition} from 'react-transition-group';
import VisibilitySensor  from 'react-visibility-sensor'

import { InfoContainer, InfoContainerRow, ImageInfo, TextInfo, TitleInfo, LineSeparator, InfoSection, InfoBtn } from '../../styles/Home'

const img1 = require('../../assets/img/img4.jpg')
const img5 = require('../../assets/img/img5.jpeg')
const img6 = require('../../assets/img/img6.jpg')

function Info() {

    const [isVisible, setIsVisible] = useState(false)

    const handleVisibilty = (isVisible) => setIsVisible(isVisible)
    
    return (
        <VisibilitySensor onChange={handleVisibilty} partialVisibility >
            <InfoSection>
                <InfoContainerRow>
                    <CSSTransition
                        classNames="fade-info"
                        in={isVisible}
                        timeout={1000}
                    >
                        <InfoContainer>
                            <ImageInfo src={img1}/>
                            <LineSeparator
                                color="rgb(88,32, 142)"
                                height="0.4vw"
                            />
                            <TitleInfo>LABORATORIO</TitleInfo>
                            <TextInfo>
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                            </TextInfo>
                        </InfoContainer>
                    </CSSTransition>
                    <CSSTransition
                        classNames="fade-info"
                        in={isVisible}
                        timeout={1000}
                    >
                        <InfoContainer>
                            <ImageInfo src={img5}/>
                            <LineSeparator
                                color="rgb(88,32, 142)"
                                height="0.4vw"
                            />
                            <TitleInfo>NUTRIÓLOGO</TitleInfo>
                            <TextInfo>
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                            </TextInfo>
                        </InfoContainer>
                    </CSSTransition>
                    <CSSTransition
                        classNames="fade-info"
                        in={isVisible}
                        timeout={1000}
                    >
                        <InfoContainer>
                            <ImageInfo src={img6}/>
                            <LineSeparator
                                color="rgb(88,32, 142)"
                                height="0.4vw"
                            />
                            <TitleInfo>PSICÓLOGO</TitleInfo>
                            <TextInfo>
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                            </TextInfo>
                        </InfoContainer>
                    </CSSTransition>
                </InfoContainerRow>
                <InfoBtn>LEER MÁS</InfoBtn>
            </InfoSection>
        </VisibilitySensor>
    )
}

export default Info
