import React, { useState, useEffect } from 'react'
import Cookies from 'js-cookie';

import Navbar from '../components/Navbar'

import { MainContainer, ContentContainer, TitleContainer } from '../styles/Date'

function MainContainerC({ children, overflowHidden, title }) {

    const [isLogged, setIsLogged] = useState(false)

    useEffect(() => {
        if(Cookies.get('payload')){ setIsLogged(true) }
    }, [])

    return (
        <MainContainer overflowHidden={overflowHidden}>
            <Navbar bgNavbar={true} isLogged={isLogged}/>
            <ContentContainer>
                {title && <TitleContainer><h1>{title}</h1></TitleContainer>}
                {children}
            </ContentContainer>
        </MainContainer>
    )
}

export default MainContainerC
