import styled from 'styled-components'

import bgHome from '../assets/img/background_home.jpg'

import { deviceMin } from './device'

const purple = "rgb(88,32, 142)"
const darkBlue = "rgb(0,47,91)"
const lightBlue = "rgb(178,223,252)"

export const MainContainer = styled.div`
    width: 100%;
    height: 100%;
    background-image: url(${bgHome});
    background-repeat: no-repeat;
    background-size: 100%;
    overflow-x: hidden;
`

export const PhoneContainer = styled.div`
    flex-direction: row;
    align-items: center;
    display: flex;
`

export const TopBarContainer = styled.div`
    width: 75%;
    height: 5%;
    background-color: ${darkBlue};
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 10%;
    padding-left: 15%;
    min-height: 50px;
`

export const TopBarText = styled.span`
    color: white;
    margin-left: 0.5rem;
    font-family: Trajan;
    font-size: 1vw;
`

export const LoginLabel = styled.a`
    padding: 0 1rem;
    height: 100%;
    background-color: ${lightBlue};
    display: flex;
    align-items: center;
    justify-content: center;
    color: ${darkBlue};
    text-decoration: none;
    font-weight: bold;
    font-family: Trajan;
`

export const TitleHome = styled.h1`
    display: flex;
    align-items: center;
    justify-content: center;
    color: ${props => props.color};
    font-size: ${props => props.fontSize};
    font-weight: 50;
    margin: 0;
    margin-left: ${props => props.marginLeft || 0};
    font-family: Trajan;
`

export const Carousel = styled.div`
    width: 100%;
    height: 95%;
    display: flex;
    align-items: center;
    flex-direction: column;
    z-index: 0;
    background-color: rgb(0,0,0);
    @media ${deviceMin.laptop} {
        min-height: 40rem
    }
`

export const CarouselBgColor = styled.div`
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
`

export const CarouselImageContainer = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: nowrap;
    
`

export const CarouselImage = styled.div`
    width: 100%;
    height: 100%;
    background-image: url(${props => props.img});
    background-repeat: no-repeat;
    background-size: 100%;
    display: flex;
    flex-shrink: 0;
    &.fade-enter{ opacity: 0; }
    &.fade-enter-active{
        opacity: 1;
        transition: opacity 500ms ease-in-out;
    }
    &.fade-exit{ opacity: 1; }
    &.fade-exit-active{
        opacity: 0;
        transition: opacity 500ms ease-in-out;
    }
`

export const CarouselBtnContainer = styled.div`
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    position: relative;
    top: -55%;
    top: calc(-50% - 1.5rem);
    margin-bottom: -5rem;
`

export const CarouselBtn = styled.button`
    width: 4rem;
    height: 4rem;
    border-radius: 2rem;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 2rem;
    background-color: rgba(255,255,255,0.2);
    &:hover{ 
        cursor: pointer; 
        background-color: rgba(255,255,255,0.5);
    }
    &:focus{ outline: none }
`

export const InfoContainerRow = styled.div`
    width: 100%;
    height: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    
`

export const InfoSection = styled.div`
    width: 80%;
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 10%;
    background-color: white;
    @media ${deviceMin.laptop} {
        min-height: 40rem
    }
`

export const InfoBtn = styled.button`
    width: 10%;
    height: 5%;
    background-color: ${purple};
    color: white;
    border: 0;
    border-radius: 0.5rem;
    font-family: Trajan;
    font-size: 1vw;
    &:hover{ cursor: pointer; }
    &:focus{ outline: none }
`

export const InfoContainer = styled.div`
    align-items: center;
    width: 30%;
    justify-content: center;
    display: flex;
    flex-direction: column;
    &.fade-info-enter{
        opacity: 0;
        transform: translate(0%, 5rem)
    }
    &.fade-info-enter-active{
        opacity: 1;
        transform: translate(0%, 0%);
        transition: all 1s;
    }
`

export const LineSeparator = styled.div`
    width: ${props => props.lineWidth};
    height: ${props => props.height};
    background-color: ${props => props.color};
    border-radius: 0.5rem;
    margin: 1.5vw 0;
`

export const ImageInfo = styled.img`
    width: 18vw;
    height: 18vw;
    border-radius: 9vw;
    object-fit: cover;
`

export const TextInfo = styled.p`
    width: 12vw;
    height: 5rem;
    border-radius: 2.5rem;
    text-align: center;
    color: ${darkBlue};
    font-size: 1vw;
`

export const TitleInfo = styled.h2`
    color: ${darkBlue};
    font-family: Trajan;
    font-size: 2vw;
    margin: 0;
`

export const LoginContainer = styled.div`
    width: 90%;
    height: 70%;
    background: linear-gradient(180deg, rgb(0,47,91), rgb(0,47,91) 50%, rgb(178,223,252) 50%, rgb(178,223,252));
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 10%;
    @media ${deviceMin.laptop} {
        min-height: 40rem
    }
`

export const ServicesSection = styled.div`
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    min-height: 40rem;
`

export const ServicesContainer = styled.div`
    width: ${props => props.width};
    height: ${props => props.height};
    background-color: ${props => props.bgColor};
    display: flex;
    flex-direction: ${props => props.flexDirection};;
    align-items: ${props => props.start ? props.flexDirection === 'row' ? 'center' : 'flex-start' : 'center'};
    justify-content: ${props => props.start ? props.flexDirection === 'row' ? 'flex-start' : props.justifyContent || 'center' : 'center'};;
    border: 0;
    padding: ${props => props.padding || '2% 0 2% 10%'};
`

export const ServicesLogoContainer = styled.div`
    width: calc(100% - 3vw);
    padding-left: 3vw;
    justify-content: center;
    display: flex;
`

export const ServicesIconContainer = styled.div`
    width: 1vw;
    height: 1vw;
`

export const ServicesContainerText = styled.p`
    color: ${props => props.color};
    margin: 0;
    width: 80%;
`

export const ServicesContainerTitle = styled.h3`
    color: ${props => props.color};
    font-size: 2vw;
    margin: 1vw 0;
    font-family: Trajan;
`

export const LoginFormContainer = styled.div`
    width: 80%;
    height: ${props => props.height || '80%'};
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: ${darkBlue};
    flex-direction: row;
    align-items: center;
    padding: 0 10%;
    min-height: 35rem;
`

export const LoginForm = styled.form`
    width: 20%;
    height: ${props => props.height};;
    background-color: rgb(236,236,236);
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 1rem 2%;
`

export const LoginTitle = styled.h4`
    color: ${darkBlue};
    margin-top: 0.5rem;
    margin-bottom: 2vw;
    text-align: center;
    font-family: Trajan;
    font-size: 1vw;
`

export const LoginFormLabel = styled.label`
    color: rgb(100,100,100);
    font-size: 1vw;
    font-family: MontserratMedium;
    margin-bottom: 0.4vw;
`

export const LoginInput = styled.input`
    width: 100%;
    background-color: ${props => props.bgColor || 'white'};
    border-radius: 0.5rem;
    border: ${props => props.border || '0'};
    text-align: center;
    margin-bottom: ${props => props.mgBottom || '1vw'};;
    padding: 0.5vw;
    font-family: ${props => props.font || 'Montserrat'};;
    margin-top: ${props => props.mgTop || '0'};
    color: black;
    &:focus{ outline: 0 }
`

export const LoginFormBtn = styled.button`
    background-color: ${darkBlue};
    width: ${props => props.width || '100%'};
    border-radius: 0.5rem;
    border: 0;
    color: white;
    margin-top: 1.5vh;
    padding: 0.3vw 0.5vw;
    font-size: 1vw;
    font-family: MontserratBold;
    margin-bottom: ${ props => props.mgBottom || '0' };
    &:hover{ cursor: pointer; }
    &:focus{ outline: none }
`

export const LoginFormForgotPasswordContainer = styled.div`
    width: 100%;
    padding-top: 3vh;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
`

export const LoginFormForgotPassword = styled.a`
    color: ${props => props.color || 'rgb(100,100,100)'};
    font-size: 0.8vw;
    margin-bottom: 0.5vh;
    text-decoration: ${props => props.txtDecoration || 'none'};;
    text-align: center;
    font-family: MontserratMedium;
    &:hover{ cursor: pointer; }
`

export const DiscountAnnouncement = styled.div`
    width: ${props => props.width };;
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
    padding: 1vw 0.5rem;
    background-color: ${darkBlue};
`

export const DiscounntTextBold= styled.h4`
    color: ${props => props.color || "white"};
    font-size: ${props => props.size };
    margin: 0;
    text-align: center;
    font-family: MontserratBold;
`


export const DiscounntText= styled.p`
    color: white;
    font-size: ${props => props.size };
    margin: 0.2rem 0;
    text-align: center;
    font-family: Montserrat;
`

export const NavbarContainer = styled.div`
    height: 8vw;
    width: 90vw;
    display: flex; 
    flex-direction: row;
    align-items: center;
    margin-bottom: -8vw;
    padding: 0 5vw;
    justify-content: space-between;
    z-index: 1;
    position: sticky;
    top: 0;
    background-color: ${props => props.color};
    transition: all 500ms;
`

export const TitleHomeContainer = styled.div`
    display: flex; 
    flex-direction: row;
    align-items: center;
`

export const NavbarOptionsContainer = styled.ul`
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;
    height: fit-content;
`

export const NavbarOption = styled.li`
    height: fit-content;
    margin: 0 1vw;
    a{
        font-size: 1.2vw;
        color:  ${props => props.color};
        font-family: Trajan;
        text-decoration: none;
    }
    &:hover{ cursor: pointer; }
`

export const NavbarLine = styled.div`
    width: ${props => props.width};
    border: 2px solid ${lightBlue};
    transform:  ${props => `translateX(${props.left}px)`};
    margin: 0;
    transition: all 0.5s;
`

export const LoginMessage = styled.span`
    color: ${props => props.color || 'red'};
    font-family: MontserratBold;
    font-size: 1vw;
    text-align: center;
    width: 100%;
`

export const ForgotPasswordForm = styled.form`
    width: 90%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 0 5%;
`

export const LineSeparatorContainer = styled.div`
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 2vw 0;
`

export const Lines = styled.div`
    width: 40%;
    border: 1px solid ${props => props.color};
`

export const TextLineSeparator = styled.div`
    color: ${props => props.color};
    font-family: Montserrat;
    font-size: 0.8vw;
`