import React from 'react'

import { LineSeparatorContainer, Lines, TextLineSeparator } from '../styles/Home'

function LineSeparator({ text, color }) {
    return (
        <LineSeparatorContainer>
            <Lines color={color}/>
            <TextLineSeparator color={color}>{text}</TextLineSeparator>
            <Lines color={color}/>
        </LineSeparatorContainer>
    )
}

export default LineSeparator
