import React from 'react'

import { ConfirmationContainer, ConfirmationTitle, LoginLink } from '../../styles/SignUp'

import { URL } from '../../variables'

function Confirmation({ message, color }) {
    return (
        <ConfirmationContainer>
            <ConfirmationTitle color={color}>
                {message}
            </ConfirmationTitle>
            {color !== "red" && <LoginLink href={`${URL}#login`}>IR A INICIO DE SESIÓN</LoginLink>}
        </ConfirmationContainer>
    )
}

export default Confirmation
