import styled from 'styled-components'

//const purple = "rgb(88,32, 142)"
const darkBlue = "rgb(0,47,91)"
const lightBlue = "rgb(178,223,252)"

export const MainContainer = styled.div`
    width: 100%;
    height: 100%;
    overflow-x: hidden;
`

export const InfoItemContainer = styled.div`
    width: 100%;
    padding-top: 10vw;
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const InfoTitleContainer = styled.div`
    width: 82vw;
    padding: 1vw 4vw;
    background-color: ${darkBlue};
    margin-bottom: 2vw;
`

export const InfoTitle = styled.h1`
    font-family: Trajan;
    color: white;
    font-size: 2vw;
    margin: 0;
`

export const InfoContent = styled.div`
    width: 88vw;
    padding: 0 1vw;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
`

export const InfoImageContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    min-height: 20vw;
`

export const InfoImage = styled.img`
    object-fit: contain;
    width: 13vw;
    height: 13vw;
    border-radius: 1vw;
`

export const InfoSocialMediaContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 13vw;
    margin: 1vw 0;
`

export const InfoBtn = styled.button`
    padding: 0.5vw 1vw;
    display: flex;
    align-items: center;
    font-size: 1vw;
    justify-content: center;
    font-family: Montserrat;
    color: white;
    background-color: ${darkBlue};
    border: 0;
    border-radius: 0.5vw;
    &:hover{ cursor: pointer; }
`

export const InfoName = styled.h2`
    font-family: MontserratMedium;
    color: black;
    font-size: 1.5vw;
    margin: 0;
    margin-bottom: 0.2vw;
`

export const InfoDescriptionContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    min-height: 20vw;
    margin-top: 1vw;
    max-width: 40vw;
`

export const InfoType = styled.h3`
    font-family: MontserratMedium;
    color: ${darkBlue};
    font-size: 1vw;
    margin: 0;
`

export const InfoDescription = styled.p`
    font-family: Montserrat;
    text-align: justify;
`

export const ReviewsContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 1vw;
    width: 30vw;
`

export const ReviewsGroup = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 1vw;
    overflow-y: hidden;
    overflow-x: hidden;
    width: 30vw;
    padding-right: 10px;
    max-height: 47vh;
    ::-webkit-scrollbar { width: 10px; }
    ::-webkit-scrollbar-track { background: #f1f1f1; }
    ::-webkit-scrollbar-thumb { background: #888; }
    ::-webkit-scrollbar-thumb:hover { background: #555; }
    &:hover{ overflow-y: auto; }
`

export const ReviewContainer = styled.div`
    width: 30vw;
    min-height: 3vw;
    background-color: ${props => props.bgColor};
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 1vw 0;
    height: ${props => props.height};
    transition: height 500ms ease;
    flex-shrink: 0;
    &:hover{ cursor: pointer; }
`

export const ReviewText = styled.p`
    max-width: 20vw;
    font-family: MontserratItalic;
    font-size: 0.8vw;
    height: 100%;
    overflow-y: hidden;
`

export const ReviewUser = styled.span`
    max-width: 20vw;
    font-family: MontserratBold;
    font-size: 0.8vw;
`

export const ReviewStars = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin-bottom: ${ props => props.mgBottom || '0' };
`

export const PostReviewContainer = styled.div`
    width: 30vw;
    flex-direction: column;
    align-items: center;
    display: flex;
`

export const PostReviewRow = styled.div`
    width: 30vw;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    display: flex;
    margin: 1vw 0; 
`

export const PostRev = styled.div`
    background-color: ${darkBlue};
    width: 24vw;
    height: 5vw;
    border-radius: 0.5rem;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 1vw;
    display: flex;
`

export const PostReviewInput = styled.textarea`
    background-color: ${lightBlue};
    width: 16vw;
    height: 2.7vw;
    border-radius: 0.5rem;
    font-family: Montserrat;
    color: black;
    border: 0;
    vertical-align: top;
    padding: 0.5vw 0.5vw;
    resize: none;
    &:focus{ outline: 0; }
    ::-webkit-scrollbar { width: 10px; }
    ::-webkit-scrollbar-track { background: #f1f1f1; }
    ::-webkit-scrollbar-thumb { background: #888; }
    ::-webkit-scrollbar-thumb:hover { background: #555; }
`

export const StarContainer = styled.div`
    &:hover{ cursor: pointer; }
`

export const PostReviewBtn = styled.button`
    width: 3vw;
    height: 3vw;
    border-radius: 1.5vw;
    background-color: ${lightBlue};
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    border: 0;
    &:hover{ cursor: pointer; }
    &:focus{outline: none; }
`

export const PostReviewLabel = styled.span`
    font-family: MontserratBold;
    font-size: 1vw;
    color: ${props => props.color};
    opacity: ${props => props.opacity};
    transition: opacity 250ms;
    text-align: center;
`

export const LabImageContainer = styled.div`
    width: 13vw;
    height: 13vw;
`