import React from 'react'
import { faFacebook, faInstagram } from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { ReactComponent as LaboratoryIcon } from '../../assets/icons/laboratory.svg';
import { ReactComponent as LogoIcon } from '../../assets/icons/logo1.svg';
import { ReactComponent as PercentageIcon } from '../../assets/icons/porcentaje.svg';
import { ReactComponent as DoctorsIcon } from '../../assets/icons/medicos.svg';
import { ReactComponent as ServicesIcon } from '../../assets/icons/servicios.svg';

import { ServicesContainer, ServicesContainerText, ServicesContainerTitle, TitleHome, ServicesSection } from '../../styles/Home'

function LoginInfo() {
    return (
        <ServicesSection>
            <ServicesContainer
                width="70%"
                height="80%"
                flexDirection="row"
                padding="0"
                start
            >
                <ServicesContainer
                    width="30%"
                    height="100%"
                    flexDirection="column"
                    padding="0"
                >
                    <ServicesContainer
                        width="90%"
                        height="50%"
                        bgColor="rgb(255,201,7)"
                        flexDirection="column"
                        start
                    >
                        <DoctorsIcon />
                        <ServicesContainerTitle color="rgb(0,47,91)">MÉDICOS</ServicesContainerTitle>
                        <ServicesContainerText color="rgb(0,47,91)">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</ServicesContainerText>
                    </ServicesContainer>
                    <ServicesContainer
                        width="90%"
                        height="50%"
                        bgColor="rgb(0,47,91)"
                        flexDirection="column"
                        start
                    >
                        <PercentageIcon />
                        <ServicesContainerTitle color="white" >DESCUENTOS</ServicesContainerTitle>
                        <ServicesContainerText color="white">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</ServicesContainerText>
                    </ServicesContainer>
                </ServicesContainer>
                <ServicesContainer
                    width="70%"
                    height="100%"
                    flexDirection="column"
                    padding="0"
                    start
                    justifyContent="flex-start"
                >
                    <ServicesContainer
                        width="100%"
                        height="70%"
                        flexDirection="row"
                        padding="0"
                    >
                        <ServicesContainer
                            width="40%"
                            height="100%"
                            bgColor="rgb(178,223,252)"
                            flexDirection="column"
                            padding="0"
                        >
                            <LogoIcon />
                            <ServicesContainer
                                width="100%"
                                flexDirection="row"
                                padding="0"
                            >
                                <TitleHome color="rgb(0,47,91)" fontSize="6vw">M</TitleHome>
                                <TitleHome color="rgb(83,24,140)" fontSize="6vw">D</TitleHome>
                            </ServicesContainer>
                            <TitleHome color="rgb(0,47,91)" fontSize="2vw">MÉDICA</TitleHome>
                        </ServicesContainer>
                        <ServicesContainer
                            width="70%"
                            height="100%"
                            flexDirection="column"
                            padding="0"
                        >
                            <ServicesContainer
                                width="90%"
                                height="50%"
                                bgColor="rgb(83,24,140)"
                                flexDirection="column"
                                start
                            >
                                <ServicesIcon />
                                <ServicesContainerTitle color="white" marginTop="0.5vh">SERVICIOS</ServicesContainerTitle>
                                <ServicesContainerText color="white">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</ServicesContainerText>
                            </ServicesContainer>
                            <ServicesContainer
                                width="100%"
                                height="50%"
                                bgColor="rgb(0,47,91)"
                                flexDirection="column"
                                padding="0"
                            >
                                <ServicesContainerTitle color="white">CONTACTO</ServicesContainerTitle>
                                <ServicesContainer
                                    width="100%"
                                    bgColor="rgb(0,47,91)"
                                    flexDirection="row"
                                    padding="0"
                                >
                                    <FontAwesomeIcon icon={faFacebook} color="white" size="3x"/>
                                    <FontAwesomeIcon icon={faInstagram} color="white" size="3x" style={{ marginLeft: '2rem' }}/>
                                </ServicesContainer>
                            </ServicesContainer>
                        </ServicesContainer>
                    </ServicesContainer>
                    {<ServicesContainer
                        width="90%"
                        height="26%"
                        bgColor="rgb(83,24,140)"
                        flexDirection="row"
                        start
                        
                    >
                        <LaboratoryIcon />
                        <ServicesContainer
                            width="90%"
                            height="100%"
                            flexDirection="column"
                            start
                        >
                            <ServicesContainerTitle color="white">LABORATORIO</ServicesContainerTitle>
                            <ServicesContainerText color="white">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</ServicesContainerText>
                        </ServicesContainer> 
                    </ServicesContainer>}
                </ServicesContainer>
            </ServicesContainer>
        </ServicesSection>
    )
}

export default LoginInfo
