import React, { useState, useEffect } from 'react'
import axios from 'axios'

import LabsGroup from '../components/labs/LabsGroup'
import MainContainer from '../components/MainContainer'

import { LabsTitleContainer } from '../styles/Labs'

import { URL } from '../variables'

function Labs() {

    const [labs, setLabs] = useState([])
    const [services, setServices] = useState([])

    const getLabs = async () => {
        let res = await axios({
            method: 'GET',
            url: `${URL}/all-labs`,
            timeout: 3000
        })
        return res.data
    }

    useEffect(() => {
        getLabs().then( ({labs, services}) => {
            setLabs(labs)
            setServices(services)
        }).catch( (e) => {
            alert('Ha sucedido un error en el servidor. Intentalo más tarde.')
        })
    }, [])

    return (
        <MainContainer overflowHidden={true} title="LABORATORIOS">
            {labs.map( (lab,idx) => (
                <LabsGroup 
                    lab={lab} 
                    services={services.filter( service => service.tipo === lab.id )} 
                    closed={idx!==0}
                    key={lab.id}
                />
            ) )}
        </MainContainer>
    )
}

export default Labs
