import React, { useRef, useState, useEffect } from 'react'

import { ReactComponent as LogoIcon } from '../assets/icons/logo-white.svg';
import { ReactComponent as LogoBlueIcon } from '../assets/icons/logo-blue.svg';

import { URL } from '../variables'

import { NavbarContainer, TitleHomeContainer, TitleHome, NavbarOption, NavbarOptionsContainer, NavbarLine } from '../styles/Home'

function Navbar({ bgNavbar, isLogged }) {

    const refHomeOption = useRef(null)
    const refNavbarContainer = useRef(null)
    const refDoctorOption = useRef(null)
    const refLabsOption = useRef(null)
    const refDateOption = useRef(null)
    const refServicesOption = useRef(null)
    const refUserOption = useRef(null)

    const [widthLineNavbar, setwidthLineNavbar] = useState(0)
    const [leftLineNavabr, setLeftLineNavabr] = useState(0)

    const handleHover = (option, widthDifference, leftDifference) => {
        let widthOffset = widthDifference || 3
        let leftOffset = leftDifference || 0
        let navbarContainerLeft = refNavbarContainer.current.offsetLeft
        let optionWidth = 0
        let optionLeft = 0
        switch(option){
            case "doctor":
                optionWidth = refDoctorOption.current.offsetWidth
                optionLeft = refDoctorOption.current.offsetLeft
                setLineNavbarProps(optionWidth - widthOffset, optionLeft - navbarContainerLeft + leftOffset)
                break;
            case "home":
                optionWidth = refHomeOption.current.offsetWidth
                optionLeft = refHomeOption.current.offsetLeft
                setLineNavbarProps(optionWidth - widthOffset, optionLeft - navbarContainerLeft)
                break;
            case "labs":
                optionWidth = refLabsOption.current.offsetWidth
                optionLeft = refLabsOption.current.offsetLeft
                setLineNavbarProps(optionWidth - widthOffset, optionLeft - navbarContainerLeft + leftOffset)
                break;
            case "date":
                optionWidth = refDateOption.current.offsetWidth
                optionLeft = refDateOption.current.offsetLeft
                setLineNavbarProps(optionWidth - widthOffset, optionLeft - navbarContainerLeft + leftOffset)
                break;
            case "services":
                optionWidth = refServicesOption.current.offsetWidth
                optionLeft = refServicesOption.current.offsetLeft
                setLineNavbarProps(optionWidth - widthOffset, optionLeft - navbarContainerLeft)
                break;
            case "user":
                optionWidth = refUserOption.current?.offsetWidth
                optionLeft = refUserOption.current?.offsetLeft
                setLineNavbarProps(optionWidth - widthOffset, optionLeft - navbarContainerLeft)
                break;
            default:
                break;
        }
    }

    const handleResize = () => {
        let homeOptionWidth = refHomeOption.current.offsetWidth
        let homeOptionLeft = refHomeOption.current.offsetLeft
        let navbarContainerLeft = refNavbarContainer.current.offsetLeft
        if(widthLineNavbar !== homeOptionWidth){ setLineNavbarProps(homeOptionWidth, homeOptionLeft - navbarContainerLeft) }
    }

    const setLineNavbarProps = (width, left) => {
        setwidthLineNavbar(width)
        setLeftLineNavabr(left)
    }

    const handleLeave = () => {
        markOptionNavbar()
    }

    const markOptionNavbar = (offsetWidth, offsetLeft) => {
        switch(window.location.pathname){
            case '/':
                handleHover('home', offsetWidth)
                break;
            case '/medicos':
                handleHover('doctor', offsetWidth, offsetLeft)
                break;
            case '/laboratorios':
                handleHover('labs', offsetWidth, offsetLeft)
                break;
            case '/cita':
                handleHover('date', offsetWidth, offsetLeft)
                break;
            case '/usuario':
                handleHover('user', offsetWidth, offsetLeft)
                break;
            default:
                if(window.location.pathname.includes('/medicos')){ return handleHover('doctor', offsetWidth, offsetLeft) }
                else if(window.location.pathname.includes('/laboratorios')){ return handleHover('labs', offsetWidth, offsetLeft) }
                handleHover('home', offsetWidth)
        }
    }

    let initialNavbarOption = () => {
        switch(window.location.pathname){
            case '/':
                markOptionNavbar(-refHomeOption.current.offsetWidth * 0.15)
                break;
            case '/medicos':
                var offsetLeft = isLogged ? 0 : refDoctorOption.current.offsetLeft * 0.01
                var offsetWidth = isLogged ? 0 : -refDoctorOption.current.offsetWidth * 0.01
                markOptionNavbar(offsetWidth, offsetLeft)
                break;
            case '/laboratorios':
                var offsetLeft = isLogged ? 0 : refLabsOption.current.offsetLeft * 0.025
                var offsetWidth = isLogged ? 0 : -refLabsOption.current.offsetWidth * 0.09
                markOptionNavbar(offsetWidth, offsetLeft)
                break;
            case '/cita':
                var offsetLeft = isLogged ? refDateOption.current.offsetLeft * 0.045 : refDateOption.current.offsetLeft * 0.038
                var offsetWidth = isLogged ? -refDateOption.current.offsetWidth * 0.09 : -refDateOption.current.offsetWidth * 0.09
                markOptionNavbar(offsetWidth, offsetLeft)
                break;
            case '/usuario':
                var offsetLeft = isLogged ? 0 : refUserOption.current?.offsetLeft * 0.038
                var offsetWidth = isLogged ? 0 : -refUserOption.current?.offsetWidth * 0.09
                markOptionNavbar(offsetWidth, offsetLeft)
                break;
            default:
                if(window.location.pathname.includes('/medicos')){
                    return markOptionNavbar(-refDoctorOption.current.offsetWidth * 0.15, refDoctorOption.current.offsetLeft * 0.01)
                }else if(window.location.pathname.includes('/laboratorios')){
                    return markOptionNavbar(-refLabsOption.current.offsetWidth * 0.09, refLabsOption.current.offsetLeft * 0.025)
                }
                markOptionNavbar(-refHomeOption.current.offsetWidth * 0.15)
        }
    }

    useEffect(() => {
        window.addEventListener('resize', handleResize)
        initialNavbarOption()
        return () => {
            window.removeEventListener('resize', handleResize)
        }
    }, [isLogged])

    return (
        <NavbarContainer color={ bgNavbar ? 'white' : 'transparent' }>
            <TitleHomeContainer>
                {bgNavbar ? <LogoBlueIcon/> : <LogoIcon/> }
                <TitleHome color={ bgNavbar ? 'rgb(0,47,91)' : 'white' } fontSize="6vw">M</TitleHome>
                <TitleHome color={ bgNavbar ? 'rgb(88,32, 142)' : 'rgb(178,223,252)' } fontSize="6vw">D</TitleHome>
                <TitleHome color={ bgNavbar ? 'rgb(0,47,91)' : 'white' } fontSize="4vw" marginLeft="1vw">MÉDICA</TitleHome>
            </TitleHomeContainer>
            <div>
                <NavbarOptionsContainer ref={refNavbarContainer}>
                    <NavbarOption 
                        onMouseEnter={() => { handleHover('home') }}
                        onMouseLeave={handleLeave}
                        ref={refHomeOption}
                        color={ bgNavbar ? 'rgb(0,47,91)' : 'white' }
                    >
                        <a href={`${URL}`}>HOME</a>
                    </NavbarOption>
                    {isLogged && (
                        <NavbarOption 
                            onMouseEnter={() => { handleHover('user') }}
                            onMouseLeave={handleLeave}
                            ref={refUserOption}
                            color={ bgNavbar ? 'rgb(0,47,91)' : 'white' }
                        >
                            <a href={`${URL}/usuario`}>USUARIO</a>
                        </NavbarOption>
                    )}
                    <NavbarOption
                        onMouseEnter={() => { handleHover('doctor') }}
                        onMouseLeave={handleLeave}
                        ref={refDoctorOption}
                        color={ bgNavbar ? 'rgb(0,47,91)' : 'white' }
                    >
                        <a href={`${URL}/medicos`}>MÉDICOS</a>
                    </NavbarOption>
                    <NavbarOption
                        onMouseEnter={() => { handleHover('labs') }}
                        onMouseLeave={handleLeave}
                        ref={refLabsOption}
                        color={ bgNavbar ? 'rgb(0,47,91)' : 'white' }
                    >
                        <a href={`${URL}/laboratorios`}>LABORATORIOS</a>
                    </NavbarOption>
                    <NavbarOption
                        onMouseEnter={() => { handleHover('services') }}
                        onMouseLeave={handleLeave}
                        ref={refServicesOption}
                        color={ bgNavbar ? 'rgb(0,47,91)' : 'white' }
                    >
                        <a href={`${URL}`}>SERVICIOS</a>
                    </NavbarOption>
                    <NavbarOption
                        onMouseEnter={() => { handleHover('discount') }}
                        onMouseLeave={handleLeave}
                        ref={refDateOption}
                        color={ bgNavbar ? 'rgb(0,47,91)' : 'white' }
                    >
                        <a href={`${URL}/cita`}>CITA</a>
                    </NavbarOption>
                </NavbarOptionsContainer>
                <NavbarLine
                    width={`${widthLineNavbar}px`}
                    left={leftLineNavabr}
                ></NavbarLine>
            </div>
        </NavbarContainer>
    )
}

export default Navbar
