import React, { useState } from 'react'
import { faPaperPlane } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import axios from 'axios'

import Stars from '../Stars'

import { PostReviewContainer, PostRev, PostReviewInput, PostReviewBtn, PostReviewRow, PostReviewLabel } from '../../styles/Info'

import { URL } from '../../variables'

function PostReview({ id, infoType }) {

    const [review, setReview] = useState('')
    const [opacityLabel, setOpacityLabel] = useState(0)
    const [stars, setStars] = useState(0)
    const [showMessageLabel, setShowLabel] = useState(false)
    const [emptyReviewErr, setEmptyReviewErr] = useState(false)
    const [postSuccessed, setPostSuccessed] = useState(false)
    const [serverReviewErr, setServerReviewErr] = useState(false)

    const handleInput = e => setReview(e.target.value)

    const post = async (data) => {
        let res = await axios({
            url: `${URL}/${infoType === "medicos" ? 'medico' : 'laboratorio'}/${id}`,
            method: 'POST',
            timeout: 5000,
            data
        })

        return res.data
    }

    const postSuccess = () => {
        setShowLabel(true)
        setPostSuccessed(true)
        setReview('')
        setStars(0)
        setTimeout(() => { setOpacityLabel(1) }, 1);
        setTimeout(() => { setOpacityLabel(0) }, 1500);
        setTimeout(() => {
            setShowLabel(false)
            setPostSuccessed(false)
        }, 5000);
    }

    const postError = (err) => {
        setShowLabel(true)
        if(err === "empty"){ setEmptyReviewErr(true) }
        if(err === "server"){ setServerReviewErr(true) }
        setTimeout(() => { setOpacityLabel(1) }, 1);
        setTimeout(() => { setOpacityLabel(0) }, 1500);
        setTimeout(() => {
            setShowLabel(false)
            if(err === "empty"){ setEmptyReviewErr(false) }
            if(err === "server"){ setServerReviewErr(true) }
        }, 5000);
    }

    const postReview = () => {
        if(review !== "" && stars !== 0){
            let postReview = {
                review,
                stars,
                user: 1
            }
            console.log(postReview)
            post(postReview).then(() => {
                postSuccess()
            }).catch(e => postError('server'))
        }else{
            postError('empty')
        }
    }

    const getMessage = () => {
        if(emptyReviewErr){ return 'No puede haber una review en blanco o con cero estrellas.' }
        if(serverReviewErr){ return 'Ha ocurrido un error en el servidor. Itentelo más tarde.' }
        if(postSuccessed){ return 'La reseña se ha publicado correctamente, está esperando su revisión.' }
    }

    const getColor = () => {
        if(!postSuccessed){ return 'red' }
        else{ return 'rgb(0,47,91)' }
    }

    return (
        <PostReviewContainer>
            <PostReviewRow>
                <PostRev>
                    <PostReviewInput 
                        onChange={handleInput}
                        value={review}
                        placeholder="Haz una reseña"
                    />
                    <Stars 
                        value={stars}
                        canSelect
                        setValue={setStars}
                    />
                </PostRev>
                <PostReviewBtn onClick={postReview}>
                    <FontAwesomeIcon 
                        icon={faPaperPlane} 
                        color="rgb(0,47,91)"
                        size="2x" 
                        className="postreview-icon-btn"
                    />
                </PostReviewBtn>
            </PostReviewRow>
            {showMessageLabel && (
                <PostReviewLabel color={getColor()} opacity={opacityLabel}>{getMessage()}</PostReviewLabel>
            )}
        </PostReviewContainer>
    )
}

export default PostReview
