import React from 'react'

import Review from './Review'
import PostReview from './PostReview'

import { ReviewsContainer, ReviewsGroup, ReviewContainer, ReviewText, ReviewUser } from '../../styles/Info'

function Reviews({ reviews, itemID, itemType }) {
    return (
        <ReviewsContainer>
            <ReviewsGroup>
                {reviews.length !== 0 ? (
                    reviews.map( (review, idx) => (
                        <Review 
                            key={review.id}  
                            review={review}
                            idx={idx}
                        />
                    ) )
                ):(
                    <ReviewContainer bgColor='rgb(246,246,246)'>
                        <ReviewText>
                            <ReviewUser>TODAVÍA NO SE HA HECHO UNA RESEÑA</ReviewUser>
                        </ReviewText>
                    </ReviewContainer>
                )}
            </ReviewsGroup>
            <PostReview id={itemID} itemType={itemType}/>
        </ReviewsContainer>
    )
}

export default Reviews
