export const defaultReducer = (state, action) => {
    switch(action.type){
        case "SET": return action.value
        default: return state
    }
}

export const addRemoveReducer = (state, action) => {
    switch(action.type){
        case "ADD": return [...state, action.value]
        case "REMOVE": 
            let newState = [...state]
            let idx = newState.findIndex( item => item.id == action.value.id )
            newState.splice(idx, 1)
            return newState
        default: return state
    }
}