import React, { useState } from 'react'
import Modal from 'react-modal';
import axios from 'axios';
import Loader from 'react-loader-spinner'

import LineSeparator from '../LineSeparator'

import { ForgotPasswordForm, LoginInput, LoginFormBtn, LoginFormForgotPassword, LoginMessage } from '../../styles/Home'

import { ReactComponent as PadlockIcon } from '../../assets/icons/candado.svg';

import { URL } from '../../variables'

Modal.setAppElement('#root')

const customStyles = {
    content : {
        top: '25%',
        left: '40%',
        right: 'auto',
        bottom: 'auto',
        width: '20%',
        height: '50%',
        borderRadius: '0',
        border: '0',
    }
};

function ForgotPassword({ modal, setModal }) {

    const [email, setEmail] = useState('')
    const [message, setMessage] = useState('')
    const [err, setErr] = useState(false)
    const [success, setSuccess] = useState(false)
    const [loading, setLoading] = useState(false)
    const [minuteControl, setMinuteControl] = useState(false)

    const closeModal = () => {
        setErr(false)
        setSuccess(false)
        setModal(false)
    }

    const handleInput = (e) => setEmail(e.target.value)

    const postForgotPassword = async () => {
        const res = await axios({
            url: `${URL}/user/password`,
            method: 'POST',
            data: { email }
        })

        return res.data
    }

    const handleMinuteControl = () => {
        setTimeout(() => {
            setMessage('¿NO TE LLEGÓ EL ENLACE? PUEDES VOLVER A ENVIARLO DENTRO DE UN MINUTO')
            setTimeout(() => {
                setMinuteControl(false)
                setMessage('')
                setSuccess(false)
            }, 1000 * 60);
        }, 1000 * 5);
    }

    const handleBtn = (e) => {
        e.preventDefault()
        if(email !== "" && !loading && !minuteControl){
            setLoading(true)
            setMinuteControl(true)
            setErr(false)
            postForgotPassword().then(({ok, message}) => {
                setLoading(false)
                setMessage(message)
                if(!ok){ return setErr(true) }
                setSuccess(true)
                handleMinuteControl(message) 
            }).catch((e) => {
                setLoading(false)
                setErr(true)
                setMinuteControl(false)
                console.log('a')
                if(e.response.status === 404){ return setMessage('DEBES COMPLETAR TODOS LOS CAMPOS') }
                setMessage('HA OCURIIDO UN ERROR EN EL SERVIDOR. INTENTALO MÁS TARDE') 
            })
        }
    }

    return (
        <Modal
            isOpen={modal}
            onRequestClose={closeModal}
            style={customStyles}
            contentLabel="Example Modal"
        >
            <ForgotPasswordForm>
                <PadlockIcon width="8vw" height="8vw"/>
                <LoginInput 
                    value={email}
                    onChange={handleInput}
                    type="email"
                    bgColor="rgb(240,240,240)"
                    border="2px solid rgb(230,230,230)"
                    mgTop="2vw"
                    placeholder="EMAIL"
                    font="MontserratMedium"
                    mgBottom={success ? '2vw' : '0'}
                />
                {!minuteControl && <LoginFormBtn width="calc(100% + 1.1vw)" onClick={handleBtn} mgBottom={err ? '1vw' : '0'}>ENVIAR ENLACE DE ACCESO</LoginFormBtn>}
                {(err || (success && minuteControl)) && <LoginMessage color={success ? 'rgb(0,47,91)' : 'red'}>{message}</LoginMessage>}
                <LineSeparator text="o" color="rgb(120,120,120)"/>
                <LoginFormForgotPassword color="black" txtDecoration="underline" href={`${URL}/registro`}>CREAR NUEVA CUENTA</LoginFormForgotPassword>
                {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
            </ForgotPasswordForm>
        </Modal>
    )
}

export default ForgotPassword
